import styled from '@emotion/styled'
import { Box, Flex } from 'rebass'
import { css, keyframes } from '@emotion/react'

import Thumb, { THUMB_SIZE } from '../../../components/Thumb'
import CONST from '../../../utils/const'
import Button from '../../../components/Button'
import { getResultText } from '../../../utils/utility'

const Title = styled('div')`
  color: white;
  width: 25%;
  text-transform: uppercase;
  text-align: center;
`

const Result = styled('div')`
  text-transform: uppercase;
  color: white;
  font-size: 30px;
`

const spinAndZoom = keyframes`
  0% {
    transform: scale(0) rotate(0deg);
  }
  100% {
    transform: scale(1) rotate(360deg);
  }
`

const spinAndZoomCss = css`
  animation: ${spinAndZoom} 0.5s ease-in-out;
`

const ResultAnnounceDesktop = ({ userChose, computerChose, result, onPlayAgainClick }) => {
  return (
    <>
      <Flex justifyContent="space-between" width="100%">
        <Title>You Picked</Title>
        <Title>The House Picked</Title>
      </Flex>
      <Flex justifyContent="space-between" width="100%" mt={6}>
        <Thumb
          thumbSize={THUMB_SIZE.MEDIUM}
          thumbType={userChose}
          hasWon={result === CONST.RESULT.USER}
        />
        <Flex
          css={spinAndZoomCss}
          flexDirection="column"
          alignItems="center"
          justifyContent="center">
          <Result>{getResultText(result)}</Result>
          <Box pt={2}>
            <Button onClick={onPlayAgainClick}>Play again</Button>
          </Box>
        </Flex>
        <Thumb
          thumbSize={THUMB_SIZE.MEDIUM}
          thumbType={computerChose}
          hasWon={result === CONST.RESULT.COMPUTER}
        />
      </Flex>
    </>
  )
}

export default ResultAnnounceDesktop
